import { Node, Document } from '@ali/4ever-cangjie';
function filterVoteCard(node: Node): Node {
  if (node.isElement()) {
    const newChildren = node.nodes.map(filterVoteCard);
    if (node.data?.cardType === 'vote') {
      return node.set('data', {
        ...node.data,
        metadata: {
          ...node.data.metadata,
          voteId: undefined,
        }
      });
    }
    return node.set('nodes', newChildren);
  }
  return node;
}

export default function filterFragment(node: Document) {
  const newNode = filterVoteCard(node);
  return newNode as Document;
}
